import { DatePipe } from '@angular/common';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NbDialogService, NbToastrService } from '@nebular/theme';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { AppTvServices } from '../../../services/app-tv/app-tv.services';
import { AppsServices } from '../../../services/apps/apps.services';
import { CategoriseServices } from '../../../services/categories/categories.services';
import { FilterToQueryService } from '../../../services/filter-to-query/filter-to-query.service';
import { StoresServices } from '../../../services/stores/stores.services';
import { indexColumn } from './index.column';
import { saveAs } from 'file-saver';
import { SharedApiServiceService } from '../../../services/shared/shared-api-service.service';
import { OffersServices } from '../../../services/offers/offers.services';
import { OfferTypesServices } from '../../../services/offer-types/offer-types.services';
import { UserService } from '../../../@core/mock/users.service';
import { UsersServices } from '../../../services/users/users.services';

@Component({
    selector: 'app-offers',
    templateUrl: 'index.component.html',
    styleUrls: ['index.component.scss'],
})
export class IndexComponent implements OnInit {
    @ViewChild('dialog') dialog: any;
    public columnGrid: any[] = indexColumn;
    public autoCompleteData: any = [
        {
            name: 'type',
            list: []
        },
        {
            name: 'owner',
            list: []
        }
    ];

    public dataItem: GridDataResult = {
        data: [],
        total: 0
    };
    public loading: boolean = false;
    public currentDeleteApp: any;

    apps = [];
    types = [];
    appOptions = [];
    appTvTypes = [];
    lastMeta: string;

    constructor(
        private objectsServices: OffersServices,
        private offerTypesServices: OfferTypesServices,
        private userService: UsersServices,
        private dialogService: NbDialogService,
        private toaster: NbToastrService,
        private router: Router,
        private convertFilter: FilterToQueryService,
        private categoryService: CategoriseServices,
        private storesServices: StoresServices,
        private _datePipe: DatePipe,
        private sharedService: SharedApiServiceService
    ) {
    }

    ngOnInit() {
        this.getTypesForSelection();
        this.getUsersForSelection();
        this.getObjects();
    }

    getTypesForSelection(meta="") {
        this.offerTypesServices.getOfferTypesForSelection(meta).subscribe(
            res => {
                console.log(res);
                this.columnGrid.filter(ele => ele.field === 'type')[0].filterMenu = res['data'].map(ele => {
                    let data = {
                        textField: ele.name,
                        valueField: ele.id
                    };
                    return data;
                });
            },
            error => {
            },
            () => {
            }
        );
    }

    getUsersForSelection(meta="") {
        console.log(meta);
        meta = meta + (meta?'&':'' )+ "type_id=2";
        this.userService.getForSelection(meta).subscribe(
            res => {
                this.autoCompleteData.filter(ele => ele.name === 'owner')[0].list = res['data'];
            }, error => {
            });
    }

    getObjects(meta?: any) {
        this.loading = true;
        this.lastMeta = meta;
        this.objectsServices.getOffers(meta).subscribe(
            res => {
                this.dataItem.data = res['data'];
                this.dataItem.total = res['total'];
            },
            error => {
            },
            () => {
                this.loading = false;
            }
        );
    }

    filterChange(event: any) {
        let newEvent: any = Object.assign({}, event);
        console.log(newEvent);
        console.log(this.convertFilter.convertToQuery(newEvent));
        this.getObjects(this.convertFilter.convertToQuery(newEvent));
    }

    autoCompleteFilterChangeCheck(event: any) {
        const autotype = event.fieldName;
        if (autotype === 'type') {
            const meta = 'name=' + event.value;
            this.getTypesForSelection(meta);
        }

        if (autotype === 'owner') {
            const meta = 'name=' + event.value;
            this.getUsersForSelection(meta);
        }
    }

    open(dialog: TemplateRef<any>) {
        this.dialogService.open(dialog);
    }

    commandActionClick(event: any) {
        switch (event.action.action) {
            case "view":
                this.router.navigate(['./offers/edit/' + event.data.id]);
                break;

            case 'delete':
                this.currentDeleteApp = event.data;
                this.open(this.dialog);
                break;
        }
    }

    export() {
        this.sharedService.exportUrl = this.lastMeta ? 'offers/export/excel?' + this.lastMeta : 'app-tv/export/excel';
        this.sharedService.exportData(this.sharedService.exportUrl).subscribe(data => {
            saveAs(data, 'app-tv.xlsx');
        }),
            error => this.toaster.show('Error downloading the file.', 'Error', { status: 'danger' });
    }
}
